import '../styles/navbar.scss';
import { useState } from 'react'
import { Link, HashRouter } from 'react-router-dom'

import Warning from './Warning'

const NavFull = () => {
  const [warning, updateWarning] = useState(false);

  const noWarning = () => updateWarning(false);
  const handleWarning = () => updateWarning(true);

  return (
    <div className="fullnav">
        <Link className="brand" to="/">Soli-Blair</Link>
        <div className="navCont">
          <Link className="direct" to="/">Play</Link>
          <Link className="direct" to="/leaderboard">Leaderboard</Link>
          <Link className="direct" to="/info">Info</Link>
        </div>
      <Warning warning={warning} handleClose={noWarning} />
    </div>
  )
}

export default NavFull
